/* Default styles for mobile */
* {
	box-sizing: border-box;
	margin-block-start: unset;
	margin-block-end: unset;
}

.content {
	margin: auto;
}

/* Styles for larger screens */
@media (min-width: 769px) {
	.content {
		width: 80%;
	}
}

@media (min-width: 1441px) {
	.content {
		width: 60%;
	}
}
